<template>
  <div class="wrap-g">
    <mt-header title="我的上传" style="background: #fff;color: #1B1B1B;">
      <router-link :to="-1" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
      <!--<mt-button icon="more" slot="right"></mt-button>-->
    </mt-header>
    <div class="flex">
      <div class="flex1" v-for="(item,index) in list" :class="activeName==item.path?'active':''" @click="handleClick(item)">
        <span>{{item.txt}}</span>
      </div>
    </div>
    <div class="main">
      <transition name="fade" mode="in-out">
        <router-view ></router-view>
      </transition>
    </div>
  </div>

</template>

<script>
  export default {
    data () {
      return {
        list:[
          {
            txt:"全部",
            path:"/uploadall"
          },
          {
            txt:"已上架",
            path:"/uploadacomplete"
          },
          {
            txt:"未上架",
            path:"/uploadasoon"
          },
          {
            txt:"审核中",
            path:"/uploadaexamine"
          }
        ],
        activeName:"/home",
      }
    },
    methods:{
      handleClick(e){
        this.activeName=e.path
        this.$router.push({ path: e.path});
      },

    },
    mounted(){
      this.activeName=this.$route.path;
    },
  }
</script>


<style lang="less" scoped>
  .flex{
    text-align: center;
    .flex1{
      line-height: 40px;
      color: #1B1B1B;
      span{
        position: relative;
      }
    }
    .flex1.active span{
      color: #00CA02;
    }
    .flex1.active span:before{
      content:'';
      position: absolute;
      bottom: -5px;
      width: 80%;
      left: 10%;
      height: 2px;
      background: #00CA02;
    }
  }
</style>



